import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { zones } from 'dump/data';
import { getAreas } from 'services/area';

const columns = [
  {
    title: 'Mã',
    dataIndex: 'id',
    key: 'id',
  },
  {
    title: 'Tên',
    dataIndex: 'label',
    key: 'label',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      zones.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#zones/form/edit/${record.id}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ListAreaPage extends Component {
  state = {
    areas: [],
  };

  componentDidMount = async () => {
    this.getAreas();
  };

  getAreas = async () => {
    const res = await getAreas();
    this.setState({ areas: res.data.items });
  };

  render() {
    const { areas } = this.state;
    return (
      <Card title="Khu vực">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#zones/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={areas} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListAreaPage />
  </Page>
);



